import linked_list from "./linked_list";
import "./style.css";

let graph_list = () => {
  //use list instead of matrix
  let array_linked_list = [];

  let set_node = (value) => {
    let new_linked_list = linked_list();
    new_linked_list.add_node(value);
    array_linked_list.push(new_linked_list);
  };

  let set_edge = (src, dist) => {
    for (let i = 0; i < array_linked_list.length; i++) {
      let list = array_linked_list[i];
      if (list.get_head_value() == src) {
        list.add_node(dist);
      }
    }
  };

  let print_graph = () => {
    for (let i = 0; i < array_linked_list.length; i++) {
      let list = array_linked_list[i];
      let list_array = list.get_array();

      for (let j = 0; j < list_array.length; j++) {
        console.log(list_array[i] + "->");
      }
    }
  };

  return { set_node, set_edge, print_graph };
};

export default graph_list;
