/**
 *
 * @param {any} value
 * @returns {{value:any,next:Function,print:Function}}
 */
let node = (value) => {
  let next = null;

  return { value, next };
};

let linked_list = () => {
  let head = null;

  let add_node = (value) => {
    let new_node = node(value);

    if (head == null) {
      head = new_node;
    } else {
      head = add_node_helper(new_node, head);
    }
  };

  let add_node_helper = (new_node, temp_node) => {
    if (temp_node == null) {
      return new_node;
    }

    temp_node.next = add_node_helper(new_node, temp_node.next);

    return temp_node;
  };

  let get_array = () => {
    let output = [];

    return get_helper(output, head);
  };

  let get_helper = (output, temp) => {
    if (temp == null) {
      return output;
    }

    output.push(temp.value);

    return get_helper(output, temp.next);
  };

  let search_node = (value) => {
    if (head == null) return "empty";

    return search_node_helper(head, value);
  };

  let search_node_helper = (temp, value) => {
    if (temp == null) return false;

    if (temp.value == value) return true;

    return search_node_helper(temp.next, value);
  };

  let remove_node = (value) => {
    if (head == null) return "empty";

    if (search_node(value) == false) return "no such value";

    if (head.next == null) {
      head = null;
      return "removed";
    }

    head = remove_node_helper(head, value);

    return "removed";
  };

  let remove_node_helper = (temp, value) => {
    if (temp == null) return null;

    if (temp.value == value) {
      return temp.next;
    }

    temp.next = remove_node_helper(temp.next, value);

    return temp;
  };

  let get_head_value = () => head.value;

  return {
    add_node,
    get_array,
    remove_node,
    search_node,
    get_head_value,
  };
};

export default linked_list;
